.lmui-popup-container {
  position: fixed;
  display: block;
  overflow: auto;
  max-height: 100%;
  max-width: 100%;
  -webkit-transition: all 0.3s ease;
  -o-transition: all 0.3s ease;
  transition: all 0.3s ease;
}
.lmui-popup-container .lmui-popup-hv {
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}
.lmui-popup-container .lmui-popup-h {
  position: absolute;
  left: 50%;
  -webkit-transform: translate(-50%, 0);
      -ms-transform: translate(-50%, 0);
          transform: translate(-50%, 0);
}
.lmui-popup-container .lmui-popup-v {
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(0, -50%);
      -ms-transform: translate(0, -50%);
          transform: translate(0, -50%);
}
.lmui-popup-left {
  left: 0;
  bottom: 0;
  height: 100%;
  -webkit-transform: translateX(-100%);
      -ms-transform: translateX(-100%);
          transform: translateX(-100%);
}
.lmui-popup-right {
  right: 0;
  bottom: 0;
  height: 100%;
  -webkit-transform: translateX(100%);
      -ms-transform: translateX(100%);
          transform: translateX(100%);
}
.lmui-popup-top {
  top: 0;
  width: 100%;
  -webkit-transform: translateY(-100%);
      -ms-transform: translateY(-100%);
          transform: translateY(-100%);
}
.lmui-popup-bottom {
  bottom: 0;
  width: 100%;
  -webkit-transform: translateY(100%);
      -ms-transform: translateY(100%);
          transform: translateY(100%);
}
.lmui-popup-center {
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%) scale(0, 0);
      -ms-transform: translate(-50%, -50%) scale(0, 0);
          transform: translate(-50%, -50%) scale(0, 0);
}
.lmui-popup-left-enter {
  -webkit-transform: translate(0, 0);
      -ms-transform: translate(0, 0);
          transform: translate(0, 0);
}
.lmui-popup-right-enter {
  -webkit-transform: translate(0, 0);
      -ms-transform: translate(0, 0);
          transform: translate(0, 0);
}
.lmui-popup-top-enter {
  -webkit-transform: translate(0, 0);
      -ms-transform: translate(0, 0);
          transform: translate(0, 0);
}
.lmui-popup-bottom-enter {
  -webkit-transform: translate(0, 0);
      -ms-transform: translate(0, 0);
          transform: translate(0, 0);
}
.lmui-popup-center-enter {
  -webkit-transform: translate(-50%, -50%) scale(1, 1);
      -ms-transform: translate(-50%, -50%) scale(1, 1);
          transform: translate(-50%, -50%) scale(1, 1);
}
.lmui-overlay {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  opacity: 0.6;
  background: #000;
}
.lmui-overlay-enter {
  -webkit-animation: lmui-overlay-in 0.3s ease;
          animation: lmui-overlay-in 0.3s ease;
}
.lmui-overlay-leave {
  -webkit-animation: lmui-overlay-out 0.3s ease forwards;
          animation: lmui-overlay-out 0.3s ease forwards;
}
@-webkit-keyframes lmui-overlay-in {
  from {
    opacity: 0;
  }
}
@keyframes lmui-overlay-in {
  from {
    opacity: 0;
  }
}
@-webkit-keyframes lmui-overlay-out {
  to {
    opacity: 0;
  }
}
@keyframes lmui-overlay-out {
  to {
    opacity: 0;
  }
}
